import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FranchisesComponent } from './franchises.component';
import { RouterModule } from '@angular/router';
import { WebheaderModule } from '@jgpl/shared/webheader/webheader.module';
import { WebfooterModule } from '@jgpl/shared/webfooter/webfooter.module';
import { MaterialModule } from '@jgpl/shared/material.module';



@NgModule({
  declarations: [FranchisesComponent],
  imports: [
    CommonModule,
    MaterialModule,
    WebheaderModule,
    WebfooterModule,
    RouterModule.forChild([
      { path: '', component: FranchisesComponent }
    ])
  ]
})
export class FranchisesModule { }
